<template>
  <div class="page">
    <Background />
    <AlertsContainer />
    <ChangeNameForm :show="changeNameFormVisibility" />
    <ChangePasswordForm :show="changePasswordFormVisibility" />
    <div class="container">
      <Header />
      <div class="profile">
        <div class="profile__names">
          <div class="profile__names__title">
            <p>Profile</p>
            <a href="#edit" @click="showChangeNameForm">edit</a>
          </div>
          <div class="profile__names__profile">
            <div class="profile__names__profile_user">
              <font-awesome-icon icon="user"></font-awesome-icon>
            </div>
            <div class="profile__names__profile_name">
              <p>{{ profile.firstName }} {{ profile.lastName }}</p>
              <p v-if="profile.email != profile.firstName">{{ profile.email }}</p>
            </div>
          </div>
        </div>
        <div class="profile__password__title">
          <p>Password</p>
          <a href="#change-password" @click="showChangePasswordForm">change</a>
        </div>
        <div class="profile__password">
          <p>•••••••••••••••••••••</p>
        </div>
      </div>
      <div class="payment_method">
        <div class="payment_method__title">
          <p>Two factor authentication</p>
          <a href="#change-payment">set up</a>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import Header from '@/components/header/Header.vue';
import Background from '@/components/views/exchange/Background';
import AlertsContainer from '@/components/partial/AlertsContainer';
import Footer from '@/components/footer/Footer';
import ChangeNameForm from '@/components/views/profile/ChangeNameForm';
import ChangePasswordForm from '@/components/views/profile/ChangePasswordForm';

export default {
  name: 'Profile',
  components: {
    AlertsContainer,
    Background,
    Footer,
    Header,
    ChangeNameForm,
    ChangePasswordForm,
  },
  computed: {
    ...mapGetters({
      changeNameFormVisibility: 'popups/changeNameVisibility',
      changePasswordFormVisibility: 'popups/changePasswordVisibility',
      profile: 'auth/profile',
    }),
    name() {
      return (
        this.profile.firstName.toLowerCase().capitalize() +
        ' ' +
        this.profile.lastName.toLowerCase().capitalize()
      );
    },
  },
  methods: {
    showChangeNameForm() {
      this.$store.commit('popups/CHANGE_NAME', true);
    },
    showChangePasswordForm() {
      this.$store.commit('popups/CHANGE_PASSWORD', true);
    },
  },
  mounted() {
    if (!this.profile) {
      this.$router.push('/');
      return
    }

    this.$store.commit('popups/CHANGE_NAME', false);
    this.$store.commit('popups/CHANGE_PASSWORD', false);

    if (this.$route.hash === '#edit') {
      this.$store.commit('popups/CHANGE_NAME', true);
    } else if (this.$route.hash === '#change-password') {
      this.$store.commit('popups/CHANGE_PASSWORD', true);
    } else if (this.$route.hash === '#change-payment') {
      this.$store.commit('popups/CHANGE_PAYMENT', true);
    }
  },
};
</script>

<style lang="sass" scoped>
@import '../scss/smart-grid'
@import '../scss/variables'

.page
  background: $v-grad
  background-size: contain

  .container
    z-index: 1
    margin-bottom: 200px

.profile, .payment_method
    margin: 0 auto
    padding: 15px
    backdrop-filter: blur(2px)
    background: $cyan-alpha
    opacity: .8
    border-radius: 5px
    display: grid
    border: 1px solid #56A4FF77
    grid-area: profile
    color: white

    @include xs-block
        width: 90%
    @include sm-block
        width: 90%
    @include md-block
        width: 70%
    @include lg-block
        width: 50%

    &__names
        border-bottom: 1px solid $cyan-alpha

    &__password
        p
            font-size: 2rem
            line-height: 3rem
            text-align: left

        &__title
            margin-top: 15px

    &__names, &__password
        width: 100%

        &__title
            width: 100%
            display: flex
            justify-content: space-between
            font-family: Montserrat

            a
                color: $light-blue
                text-decoration: none
                font-weight: 600
                text-transform: uppercase
                font-size: 12px

                &:hover
                    color: white

            p
                color: white


        &__profile
            display: grid
            width: 100%
            grid-template-columns: 100px 1fr
            justify-items: center
            align-items: center
            height: 80%

            &_user
                border-radius: 100%
                width: 80px
                height: 80px
                background-color: $cyan
                overflow: hidden
                justify-self: end
                display: grid
                margin: 40px 0

                svg
                    width: 50%
                    height: 50%
                    place-self: center

            &_name
                margin-left: 20px
                justify-self: start

                p
                    text-align: left

                    &:first-child
                        font-size: 1.6rem
                        line-height: 2.1rem

.payment_method
    margin-top: 20px

    &__title
        width: 100%
        display: flex
        justify-content: space-between
        font-family: Montserrat

        a
            color: $light-blue
            text-decoration: none
            font-weight: 600
            text-transform: uppercase
            font-size: 12px

            &:hover
                color: white

        p
            color: white
</style>
